html {
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.header {
  text-transform: uppercase;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.nav {
  margin-left: 0;
}

a {
  align-items: center;
  height: 60px;
  margin-left: 0;
  display: flex;
}

.nav__link {
  color: #000;
  margin: 10px;
  line-height: 60px;
  text-decoration: none;
}

.nav__list {
  flex-direction: row;
  align-items: center;
  height: 60px;
  margin-left: 0;
  padding-left: 0;
  line-height: 60px;
  list-style: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__list .nav__item:first-child .nav__link {
  margin-left: 0;
}

.nav__list .nav__item:last-child .nav__link {
  margin-right: 0;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active span {
  display: inline-block;
  position: relative;
}

.nav__link.is-active span:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
